var aliyunImg;
import avatar from '../../components/yq-avatar/yq-avatar.vue';
export default{
	components: {
		avatar
	},
	onLoad() {
		let that = this;
		uni.getStorage({
			key: 'user',
			success: function(res) {
				that.userDto = res.data;
			}
		});
	} ,
	data() {
		return {
		photoAlbumDto:{},
		userDto:{},
		content:'',
		albumtitle:'',
		Tip:'',
		};
	},
	methods: {  
		clear(){
			this.Tip='';
		},
	addphotoAlbum(photoAlbumDto){
		if(this.albumtitle ==''){
			this.Tip ='相册名不能为空'
		}else{
			photoAlbumDto = this.photoAlbumDto;
			uni.request({
				url:this.$baseUrl+'/photoalbum/apa',
				method:"POST",
				data:{
					userId:this.userDto.id,
					introduction:this.content,
					name:this.albumtitle,
				},
				success : res =>{
					if(res.data.code ===1){
						uni.showToast({
							title:res.data.msg
						})
						uni.navigateTo({
							url:'/pages/addPhoto/addPhoto'
						}),
						uni.setStorage({
							key:'id',
							data:res.data.data.id
						})
					}else{
						uni.showToast({
							title:res.data.res
						})
					}
				}
			})
			
		}
	}
	},
}
